import React, { Component } from 'react';
import { Provider, connect } from 'react-redux';
import '../index.css';
import 'antd-mobile/dist/antd-mobile.css';
import { Button, Flex, WhiteSpace, List, InputItem, PickerView, 
  Modal,Tabs, Badge, TextareaItem} from 'antd-mobile';
//import DatePicker from 'antd-mobile/lib/date-picker'; 
//import 'antd-mobile/lib/date-picker/style/css';
import Layout from '../../layout/container/Layout';
//import Request from 'react-http-request';
import * as actions from '../actions/index';
import { createForm } from 'rc-form';

const axios = require('axios');
const Item = List.Item;
const Brief = Item.Brief;

const tabs = [
  { title: <Badge text={''}>公司信息</Badge> },
  { title: <Badge text={''}>人员</Badge> }
];


const province = [
  {
    label: '北京',
    value: '01',
    children: [
      {
        label: '东城区',
        value: '01-1',
      },
      {
        label: '西城区',
        value: '01-2',
      },
      {
        label: '崇文区',
        value: '01-3',
      },
      {
        label: '宣武区',
        value: '01-4',
      },
    ],
  },
  {
    label: '浙江',
    value: '02',
    children: [
      {
        label: '杭州',
        value: '02-1',
        children: [
          {
            label: '西湖区',
            value: '02-1-1',
          },
          {
            label: '上城区',
            value: '02-1-2',
          },
          {
            label: '江干区',
            value: '02-1-3',
          },
          {
            label: '下城区',
            value: '02-1-4',
          },
        ],
      },
      {
        label: '宁波',
        value: '02-2',
        children: [
          {
            label: 'xx区',
            value: '02-2-1',
          },
          {
            label: 'yy区',
            value: '02-2-2',
          },
        ],
      },
      {
        label: '温州',
        value: '02-3',
      },
      {
        label: '嘉兴',
        value: '02-4',
      },
      {
        label: '湖州',
        value: '02-5',
      },
      {
        label: '绍兴',
        value: '02-6',
      },
    ],
  },
];
class Root extends Component {
  
  constructor(props) {
    super(props);

    this.state = {
      isLoading: true,
      selectedIndex : 0
    }
  }

  componentDidMount(){
    this.props.dispatch(actions.getShop(1, this.onGetShopCallback.bind(this)));
  }

  onGetShopCallback(data) {
    this.setState({shop: data, modifyable: false});
  }

  onSaveBtnClick() {
    let shop = this.props.form.getFieldsValue();
    this.props.dispatch(actions.addShop(shop, this.onSaveBtnClickCallback.bind(this)));
  }

  onSaveBtnClickCallback(result) {
    this.props.dispatch(actions.getShop(1, this.onGetShopCallback.bind(this)));
  }

  onModifyBtnClick() {
    this.setState({modifyable: true});
  }


  onCityPickViewChange(val) {

    console.log(val);

  }

  showModal (e){
    e.preventDefault(); // 修复 Android 上点击穿透
   this.setState({city: true});
  }
  onModalClose(e){
    this.setState({city: false});
  }

  onWrapTouchStart(e){
    // fix touch to scroll background page on iOS
    if (!/iPhone|iPod|iPad/i.test(navigator.userAgent)) {
      return;
    }
    const pNode = closest(e.target, '.am-modal-content');
    if (!pNode) {
      e.preventDefault();
    }
  }

  renderBaseInfo(shop, getFieldProps) {

    if(shop == null) {
      shop = {};
    }

    if(this.state.modifyable) {
      return <List renderHeader={() =>  {return <Flex justify="end"><Button size="small" onClick={this.onSaveBtnClick.bind(this)}>保存</Button></Flex>}}>
      <InputItem  {...getFieldProps('id', {initialValue: shop.id})} editable={false}>编号</InputItem>
      <InputItem  {...getFieldProps('name', {initialValue: shop.name})} placeholder="名称">名称1</InputItem>
      <InputItem  {...getFieldProps('area_id',{initialValue: shop.area_id})} placeholder="城市"
        onClick={this.showModal.bind(this)}>城市</InputItem>
      <InputItem  {...getFieldProps('addr',{initialValue: shop.addr})} placeholder="地址">地址</InputItem>
      <InputItem  {...getFieldProps('manager',{initialValue: shop.manager})} placeholder="联系人">联系人</InputItem>
      <InputItem  {...getFieldProps('phone',{initialValue: shop.phone})} placeholder="电话">电话</InputItem>
      <InputItem  {...getFieldProps('email',{initialValue: shop.email})} placeholder="邮箱">邮箱</InputItem>
      <TextareaItem {...getFieldProps('description', {initialValue: shop.description})} placeholder="简介" rows={5} count={100} />

      <WhiteSpace/>
      
    </List>;

    } else {
      return <List renderHeader={() => {return <Flex justify="end"><Button size="small" onClick={this.onModifyBtnClick.bind(this)}>修改</Button></Flex>}}>
         
          <Item extra={shop.name}>公司</Item>
          <Item extra={shop.city}>城市</Item>
          <Item extra={shop.addr}>地址</Item>
          <Item extra={shop.manager}>联系人</Item>
          <Item extra={shop.phone}>电话</Item>
          <Item extra={shop.email}>邮箱</Item>
          <Item wrap>{shop.description}</Item>
        </List>;
    }
  }

  render() { 
    const { getFieldProps } = this.props.form;
    const {shop}  = this.state;
    return (
      <Layout pageName="deviceList">
        <Tabs tabs={tabs}
            initialPage={0}
            onChange={(tab, index) => { console.log('onChange', index, tab); }}
            onTabClick={(tab, index) => { console.log('onTabClick', index, tab); }}>


        {
          this.renderBaseInfo(shop, getFieldProps)
        }
        

      <div>
        meber
      </div>
      </Tabs>
      <Modal
          visible={this.state.city}
          transparent
          maskClosable={false}
          onClose={this.onModalClose.bind(this)}
          title="选择城市"
          footer={[{ text: '确定', onPress: () => { console.log('ok'); this.onModalClose(); } }]}
          wrapProps={{ onTouchStart: this.onWrapTouchStart }}>
        <PickerView onChange={this.onCityPickViewChange.bind(this)}
          data={province}
          value={['02', '02-1', '02-1-1']}/>
      </Modal>

    </Layout>
    );
  }
}




const RootWrapper = createForm()(Root);

// export default Root;

function mapStateToProps(state) {
  return {
    state
  };
}

export default connect((state) => {
  return state;
})(RootWrapper);



