'use strict';


export function addShop(shop, callback, errorCallback) {
    return (dispatch) => {
        axios.post('/api/shop/add', shop)
        .then(function (response) {
            let data = response.data.result;
            if(callback){
              callback(data);
            }
          }).catch(function (error) {
            if(errorCallback) {
                errorCallback(error);
            }
          }).then(function () {
            // always executed
          })
    };
}

export function getShop(shopId, callback, errorCallback) {
    return (dispatch) => {
        axios.post('/api/shop/getShop', {shopId})
        .then(function (response) {
            let data = response.data.result;
            if(callback){
              callback(data, shopId);
            }
          }).catch(function (error) {
            if(errorCallback) {
                errorCallback(error);
            }
          }).then(function () {
            // always executed
          })
    };
}


